import React, { useEffect } from "react";
import { Images } from "themes";
import { FlatList } from "react-native";
import { useDispatch, useSelector } from "react-redux";
import get from "lodash/get";
import ProgramBlock from "./ProgramBlock";
import LycheeActions from "store/lychee/actions";
import doNothing from "utils/doNothing";
import { useNavigation } from "@react-navigation/native";
import { StyleSheet } from "react-native";
import { WIDTH_RATIO } from "themes/Dimens";

const PartnerProgramsScreen = () => {
  const navigation = useNavigation();
  const dispatch = useDispatch();

  const locale = useSelector((state) => get(state, "language.locale"));
  const phone = useSelector((state) => get(state, "customer.phone", false));

  useEffect(() => {
    dispatch(
      LycheeActions.fetchLycheeTransactionHistory(
        { page: 1 },
        doNothing,
        doNothing
      )
    );
    dispatch(
      LycheeActions.getUserInfoInLycheeApp(
        phone,
        () => {},
        () => {}
      )
    );
  }, []);

  const isPanasonicMember = useSelector((state) =>
    get(state, "customer.is_customer_info_ready", false)
  );

  const featureLycheeEnable = useSelector((state) =>
    get(state, "configs.features.lychee")
  );

  const featurePanasonicEnable = useSelector((state) =>
    get(state, "configs.features.panasonic")
  );

  const featureEveronEnable = useSelector((state) =>
    get(state, "configs.features.everon")
  );

  const featureOPVEnable = useSelector((state) =>
    get(state, "configs.features.opv")
  );

  const featureDuluxEnable = useSelector((state) =>
    get(state, "configs.features.dulux")
  );

  const customerInfo = useSelector((state) =>
    get(state, "lychee.customerInfo")
  );

  const isEnoughInfo = customerInfo?.phone && customerInfo?.name;

  const onPanasonicPress = () => {
    const screenName = isPanasonicMember
      ? "PanasonicOverviewScreen"
      : "PanasonicOnboardingScreen";
    navigation.navigate(screenName, { needGoBackNormally: true });
  };

  const onLycheePress = () => {
    const screenName = isEnoughInfo
      ? "LycheeOverviewScreen"
      : "LycheeOnboardingScreen";
    navigation.navigate(screenName, { needGoBackNormally: true });
  };

  const onMasterisePress = () => {
    navigation.navigate("MasteriseHome");
  };

  const onEveronPress = () => {
    navigation.navigate("EveronOnboardingScreen");
  };

  const onDuluxPress = () => {
    navigation.navigate("DuluxOnboardingScreen");
  };

  const onOPVPress = () => {
    navigation.navigate("OPVOnboardingScreen");
  };

  const partnerPrograms = [
    {
      title:
        locale === "vi"
          ? "Ưu đãi dành cho khách hàng thuộc chương trình của RVOPV Pharmaceutical"
          : "Ưu đãi dành cho khách hàng thuộc chương trình của RVOPV Pharmaceutical",
      logo: Images.OPVLogo,
      banner: Images.OPVHomeBanner,
      onPress: onOPVPress,
      featureEnable: featureOPVEnable,
      brandName: "RVOPV Pharmaceutical"
    },
    {
      title:
        locale === "vi"
          ? "Chương trình ưu đãi dành cho cửa hàng và nhà thầu Dulux"
          : "Chương trình ưu đãi dành cho cửa hàng và nhà thầu Dulux",
      logo: Images.DuluxLogo,
      banner: Images.DuluxHomeBanner,
      onPress: onDuluxPress,
      featureEnable: featureDuluxEnable,
      brandName: "Dulux"
    },
    {
      title:
        locale === "vi"
          ? "Chương trình ưu đãi dành cho khách hàng của Everon."
          : "Chương trình ưu đãi dành cho khách hàng của Everon.",
      logo: Images.EveronLogo,
      banner: Images.EveronShortBG,
      onPress: onEveronPress,
      featureEnable: featureEveronEnable,
      brandName: "Everon"
    },
    {
      title:
        locale === "vi"
          ? "Chương trình dành cho thành viên của 82X Club khi nhận thưởng."
          : "Reward program for 82X Club membership.",
      logo: Images.LogoLychee,
      banner: Images.LycheeShortBG,
      onPress: onLycheePress,
      featureEnable: featureLycheeEnable,
      brandName: "82X Club"
    },
    {
      title:
        locale === "vi"
          ? "Chương trình dành cho các sales của Panasonic khi nhận thưởng."
          : "Reward program for Panasonic sales.",
      logo: Images.LogoPanasonic,
      banner: Images.PanasonicShortBG,
      onPress: onPanasonicPress,
      featureEnable: featurePanasonicEnable,
      brandName: "Panasonic"
    },
    {
      title:
        locale === "vi"
          ? "Chương trình dành cho các sales của Masterise khi nhận thưởng."
          : "Reward program for Masterise sales.",
      logo: Images.IconMasteriesLogo,
      banner: Images.MasteriseShortBG,
      onPress: onMasterisePress,
      featureEnable: true,
      brandName: "Masterise"
    }
  ];

  return (
    <FlatList
      data={partnerPrograms}
      renderItem={({ item: program, index }) => (
        <ProgramBlock key={index?.toString()} program={program} />
      )}
      showsVerticalScrollIndicator={false}
      contentContainerStyle={styles.body}
    />
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "white"
  },
  body: {
    paddingHorizontal: 15 * WIDTH_RATIO,
    paddingTop: 24,
    paddingBottom: 40
  },
  boldTxt: {
    fontWeight: "bold"
  }
});

export default PartnerProgramsScreen;
