import { View, StyleSheet, TouchableOpacity, Text } from "react-native";
import ImageComponent from "components/ImageComponent";
import React from "react";
import { ZEPLIN_WIDTH_RATIO } from "themes/Dimens";
import { Colors } from "themes";

const ProgramBlock = ({ program }) => {
  return program.featureEnable ? (
    <TouchableOpacity
      onPress={program.onPress}
      handleConnection
      style={styles.container}>
      <ImageComponent
        style={styles.banner}
        source={program.banner}
        resizeMode="cover"
      />
      <View style={styles.contentContainer}>
        <ImageComponent
          style={styles.logo}
          source={program.logo}
          resizeMode="contain"
        />
        <View style={styles.textContainer}>
          <Text style={styles.brandName}>{program.brandName}</Text>
          <Text style={styles.title}>{program.title}</Text>
        </View>
      </View>
    </TouchableOpacity>
  ) : null;
};

const styles = StyleSheet.create({
  container: {
    borderRadius: 6,
    backgroundColor: "#ffffff",
    marginBottom: 20,
    shadowColor: "#000",
    shadowOffset: {
      width: 0,
      height: 2
    },
    shadowOpacity: 0.2,
    shadowRadius: 4,
    elevation: 5,
    borderColor: Colors.darkGrey
  },
  contentContainer: {
    flexDirection: "row",
    padding: 16,
    alignItems: "center"
  },
  banner: {
    height: 180 * ZEPLIN_WIDTH_RATIO,
    borderTopLeftRadius: 6,
    borderTopRightRadius: 6
  },
  logo: {
    width: 60,
    height: 60
  },
  textContainer: {
    flex: 1,
    marginLeft: 16
  },
  brandName: {
    fontSize: 16,
    fontWeight: "700",
    marginBottom: 4
  },
  title: {
    fontSize: 14
  }
});

export default ProgramBlock;
